<template>
  <router-view />
</template>

<script>
import { throws } from "assert";
import { mapMutations } from "vuex";
export default {
  name: "App",

  data: () => ({
    user: "",
    //
  }),
  async created() {
    let response = await fetch("/info", {
      method: "GET",
      credentials: "include",
      mode: "cors",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.getLtik(),
      },
    });
    let result = await response.json();
    if (result) {
      this.setLogin();
      this.setId(result.id);
      this.setName(result.name);
      this.setEmail(result.email);
      this.$router.push({ name: "Home" });
    }
  },
  methods: {
    ...mapMutations([
      "setId",
      "setLogin",
      "setName", // map `this.increment()` to `this.$store.commit('increment')`
      "setEmail", // map `this.incrementBy(amount)` to `this.$store.commit('incrementBy', amount)`
    ]),
    getLtik() {
      const searchParams = new URLSearchParams(window.location.search);
      const ltik = searchParams.get("ltik");
      if (!ltik) {
        return alert("Missing lti key.");
      } else {
        localStorage.setItem("ltik", ltik);
        return ltik;
      }
    },
  },
};
</script>
